<template>
	<div>
		<mt-header fixed title="血压提醒">
  		  <router-link to="/healthData" slot="left">
  		    <mt-button icon="back"></mt-button>
  		  </router-link>
  		  <router-link to="" slot="right">
  			   <img src="../../assets/addicon.png" width="25" height="25" @click="open('picker3')">
  		  </router-link>
  		</mt-header>
      <div style="margin-top:49px">
      <mt-datetime-picker ref="picker3" type="time" v-model="value3" @confirm="handleChange">
    </mt-datetime-picker>
</template></div>
	</div>
</template>
<script>
  export default{

    data(){
       var getHours = new Date().getHours();
       var getMinutes = new Date().getMinutes();
       var current = getHours +':'+ getMinutes
       return {
        value3:current
       }
    },
    methods:{
      open(picker) {
        this.$refs[picker].open();
      },
     handleChange(value) {
        this.$message({
          message: '已选择 ' + value.toString(),
          position: 'bottom'
        });
      }
    },
  }
</script>
<style>


</style>



