<template>
	<div class="create">
		<mt-header title="兴趣爱好" class="createName">
			<router-link to="/informate" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header>
		<mt-cell>
			<div class="hobby">
				<el-tag v-for="tag in tags" :closable="true" :type="tag.type" :key="tag" :close-transition="false" @close="handleClose(tag)">{{tag.name}}</el-tag>
				<p>职业</p>
			</div>
		</mt-cell>
		<mt-cell>
			<div class="hobby">
				<el-tag v-for="job in jobs" :closable="true" :type="job.type" :key="job" :close-transition="false" @close="jobClose(job)">{{job.name}}</el-tag>
			</div>
		</mt-cell>
		<div class="next">
			<router-link to="/">
				<mt-button class="next-btn">创建账户</mt-button>
			</router-link>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			return {
				radio: '1',
				tags: [
		          { key: 1, name: '读书', type: '' },
		          { key: 2, name: '书法', type: 'gray' },
		          { key: 5, name: '绘画', type: 'primary' },
		          { key: 3, name: '音乐', type: 'success' },
		          { key: 4, name: '跑步', type: 'warning' },
		          { key: 6, name: '广场舞', type: 'danger' },
		          { key: 7, name: '太极拳', type: 'danger' },
		          { key: 8, name: '乒乓球', type: 'danger' },
		          { key: 9, name: '旅游', type: 'danger' },
		          { key: 10, name: '羽毛球', type: 'danger' }
		        ],
		        jobs: [
		          { key: 1, name: '教师', type: '' },
		          { key: 2, name: '医生', type: 'gray' },
		          { key: 5, name: '律师', type: 'primary' },
		          { key: 3, name: '公务员', type: 'success' },
		          { key: 4, name: '工程师', type: 'warning' },
		          { key: 6, name: '自由职业者', type: 'danger' },
		          { key: 7, name: '企业家', type: 'danger' },
		          { key: 8, name: '其他', type: 'danger' }
		        ]
			}
		},
		methods: {
	      		handleClose(tag) {
	        	this.tags.splice(this.tags.indexOf(tag), 1);
     		},
	     	jobClose(job) {
		        this.jobs.splice(this.jobs.indexOf(job), 1);
	     	}
    	}
	}
</script>
<style>
.hobby{
	margin-top: 10px;
}
.hobby span{
	margin-right: 10px;
	margin-top: 10px;
}
.hobby p{
	margin-top:15px;
	margin-left:45%;
}
</style>



