<template>
	  <div>
	  	<div>
	  		<mt-header fixed title="健康数据">
	  		  <router-link to="/" slot="left">
	  		    <mt-button icon="back"></mt-button>
	  		  </router-link>
	  		  <router-link to="" slot="right">
	  			<img src="../../assets/addicon.png" width="25" height="25" @click="remid()">
	  		  </router-link>
	  		</mt-header>
	  		<div style="margin-top:49px;">
		  		<el-row :gutter="20" class="an_niu">
		  		  <el-col :span="12"><p>血压</p></el-col>
		  		  <el-col :span="4">
		  		  	<p><el-button size="small" class="button_2">删除</el-button></p>
		  		  </el-col>
		  		  <el-col :span="4">
		  		  <router-link to="/remindHealth">
		  		  	<p><el-button size="small" class="button_1" name='xueya'>提醒</el-button></p>
		  		  </router-link>
		  		  </el-col>
		  		  <el-col :span="4">
		  		  	<p><el-switch
	  		  		  v-model="xueya"
	  		  		  on-color="#A21A28" off-color="#C0CCDA" on-text="" off-text="">
	  		  		</el-switch></p>
		  		  </el-col>
		  		</el-row>
		  		<el-row :gutter="20" class="an_niu">
		  		  <el-col :span="16"><p>血糖</p></el-col>
		  		  <el-col :span="4">
		  		  	<p><el-button size="small" class="button_1" name='xuetang'>提醒</el-button></p>
		  		  </el-col>
		  		  <el-col :span="4">
		  		  	<p><el-switch
	  		  		  v-model="xuetang"
	  		  		  on-color="#A21A28" off-color="#C0CCDA" on-text="" off-text="">
	  		  		</el-switch></p>
		  		  </el-col>
		  		</el-row>
	  		</div>
	  	</div>
	  </div>
</template>
<script>
	export default {
	    data() {
	      return {
	       	xueya: true,
	        xuetang: false
	      }
	    },
	    methods:{
	    	remid(){
	    		this.$prompt('名称','添加关注数据',{
	    			confirmButtonText: '确定',
          			cancelButtonText: '取消',
	    		})
	    	}
	    }
  	};
</script>
<style>
	.an_niu{
		border-bottom: 1px solid #EAEAEA;
		width: 96%;
		margin-left: 2% !important;
	}
	.an_niu p{
		display: inline-block;
	}
	.button_1{
		color:#A21A28;
		border: 1px solid #A21A28;
	}
	.button_2{
		color:#3D3D3D;
		border: 1px solid #3D3D3D;
	}
	.el-message-box{
		width: 90%;
	}
	/*el-message-box__title{
		text-align: center;
	}*/
	.el-message-box__message{
		display: inline-block;
	}
	.el-message-box__input{
		margin-left: 20px;
    	display: inline-block;
		width: 80%;
	}

</style>



