<template>
  <el-row>
    <el-col :span="6" v-for="item in list">

      <div class="cards" @click="cardDetails(item)">
          {{item.title}}
      </div>
    </el-col>
  </el-row>
</template>

<script>
export default {
  data (){
    return {
      list:[
        {
          title:"血压",
          type:"a"
        },
        {
          title:"血糖",
          type:"b"
        },
        {
          title:"体温",
          type:"c"
        },
        {
          title:"血小板",
          type:"d"
        },
        {
          title:"红细胞",
          type:"d"
        }
      ]
    }
  },
  methods:{
    cardDetails: (item)=>{
      alert(item.title)
    },
  }
}

</script>

<style>
.cards {
  display: block;
  padding:16px 8px;
  margin:10px auto;
  text-align: center;
  width: 60%;
  border: 1px solid #d3dce6;
  border-radius: 4px;
  background-color: #fff;
  overflow: hidden;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
}
</style>



