<template>
	<div class="create">
		<mt-header title="基本信息" class="createName">
			<router-link to="/createName" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header>
		<div>
			<mt-field label="姓名" placeholder="请输入姓名"></mt-field>
			<mt-cell title="性别" class="informate">
				<el-radio class="radio" v-model="radio" label="1">男</el-radio>
				<el-radio class="radio" v-model="radio" label="2">女</el-radio>
			</mt-cell>
			<mt-cell title="年龄" class="age-input">
				<el-input v-model="input"></el-input>岁
			</mt-cell>
			<mt-cell title="血型" class="el-btn">
				<div>
					<el-button v-for="item in items" :key="item.key">{{item.name}}</el-button>
				</div>
			</mt-cell>
			<mt-cell title="子女数" class="el-btn">
				<el-button>1</el-button>
				<el-button>2</el-button>
				<el-button>3</el-button>
				<el-button>其他</el-button>
			</mt-cell>
		</div>
		<div class="next">
			<router-link to="/hobby">
				<mt-button class="next-btn">下一步</mt-button>
			</router-link>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			return {
				radio: '1',
				input: '',
				items: [
					{ key: 1, name: 'A'},
					{ key: 2, name: 'B'},
					{ key: 3, name: 'AB'},
					{ key: 4, name: 'O'},
					{ key: 5, name: '其他'}
				]
			}
		}
	}
</script>
<style>
.informate .mint-cell-value{
	position: absolute;
	top: 15px;
	right: 115px;
}
/*.el-btn .mint-cell-value{
	position: absolute;
	top: 15px;
	right: 40px;
}*/
.age-input .el-input{
	width: 25%;
	right: 5px;
}
.age-input .el-input input{
	height: 30px;
}
.el-btn .el-button{
	padding: 5px 10px;
}
</style>



