</template>
<script>
	export default{
		data(){
			var buttons = [{name:'体检报告'},{name:'CT'},{name:'B超'},{name:'血常规'}];
			console.log(buttons)
			return {
				value:null,
				buttons:buttons
			}
		}
	}
</script>
<style>
	.mint-searchbar-core{
		width: 100%;
	}
	.mint-search{
		height: 8vh !important;
	}
	li{
		list-style: none;
	}
	.btn_s{
		margin-right: 20px;
		border-radius: 4px;
		background: #fff;
		height: 41px;
		padding: 10px 23px;
		border-style: none;
		border: 1px solid #d3dce6;
		box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
	}
</style>


