<template>
	<div class="create">
		<mt-header title="创建账户" class="createName"></mt-header>
		<div class="msg">
			<mt-field label="手机号码" placeholder="请输入手机号码" type="tel" v-model="form.username"></mt-field>
			<mt-field label="验证码" placeholder="请输入验证码" type="number" class="msg-btn" v-model="code">
  				<input type="button" class="click-btn" value="点击获取" v-on:click="getCode">
			</mt-field>
			<mt-field label="密码" placeholder="请输入密码" type="password" v-model="form.password"></mt-field>
			<mt-field label="确认密码" placeholder="请输入确认密码" type="password" v-model="confirm_password"></mt-field>
		</div>
		<div class="next">
			<router-link to="/informate">
			    <mt-button class="next-btn" @click.native="clickNext()" :disabled="!isNext">下一步</mt-button>
			</router-link>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			return {
				form: {
					username: '',
					password: ''
				},

				code: '',
				confirm_code:12345,
				confirm_password: ''
			}
		},
		computed:{
			checkPassword:function() {
				return (this.form.password!=''&&this.form.password == this.confirm_password)
			},
			checkCode:function() {
				return (this.code!=''&&this.code == this.confirm_code)
			},
			isNext:function(){
				return (this.form.username!=''&&(this.code!=''&&this.code == this.confirm_code)&&(this.form.password!=''&&this.form.password == this.confirm_password))
			}
		},
		methods: {
			getCode (){
				console.log(123);
				// var wait = 60;
				// if (wait == 0) {
				// 	o.removeAttribute('disabled');
				// 	o.value = '免费获取';
				// 	wait = 60;
				// } else {
				// 	o.setAttribute('disabled', true);
				// 	o.value = '重新发送'+ wait + 's';
				// 	wait --;
				// 	setTimeout(function() {
				// 		getCode (o)
				// 	}, 1000)
				// }
			},


			clickNext (){
				window.localStorage.form = JSON.stringify(this.form);

			}
		}

	}
</script>
<style>
.create{
	font-family: '微软雅黑';
}
.createName{
	background-color: #fff;
	color: #000;
}
.createName h1{
	font-size: 18px;
}
.msg,.next{
	margin-top: 50px;
	padding: 0 20px;
}
.msg .mint-field-other{
	top: 8px;
	right: 10px;
	border-radius: 3px;
}
.msg .mint-field-other .click-btn{
	padding: 0px 5px;
	height: 30px;
	font-size: 13px;
	border-radius: 15px;
	border: 1px solid #e4393c;
	background-color: #fff;
	color: #e4393c;
	outline: none;
}
.next .next-btn{
	background-color: #A21A28;
	font-weight: bold;
	color: #fff;
	width: 100%;
}



</style>



